.gacha-ranks {
  padding: 20px;
  display: flex;
}

.gacha-ranks .gacha-top-ranks,
.gacha-ranks .gacha-rank-list {
  width: 50%;
}

.gacha-ranks .gacha-rank-list {
  font-size: 16px
}

.gacha-ranks .gacha-rank-list ol {
  padding-top: 25px;
}

.gacha-ranks .gacha-rank-list ol li {
  padding-left: 15px;
  padding-bottom: 10px;
}

/* Mobile */
@media screen and (max-width: 800px) {
  .gacha-ranks {
    display: block;
  }

  .gacha-ranks .gacha-top-ranks,
  .gacha-ranks .gacha-rank-list {
    width: 100%;
  }
}
