.rank-item {
  border-radius: 100px;
  border: 3px solid white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 15px auto;
}

.rank-item.top-1 {
  width: 200px;
  height: 200px;
  font-size: 18px;
  border-color: gold;
  background: rgba(255, 215, 0, 0.3); /* gold + alpha */
}
.rank-item.top-2 {
  width: 180px;
  height: 180px;
  font-size: 16px;
  border-color: silver;
  background: rgba(192, 192, 192, 0.3); /* silver + alpha */
}
.rank-item.top-3 {
  width: 160px;
  height: 160px;
  font-size: 14px;
  border-color: brown;
  background: rgba(165, 42, 42, 0.3); /* brown + alpha */
}

.rank-medal-icon {
  font-size: 3em;
}

.rank-username {
  font-weight: bold;
}

