/* Global */
.gacha-home {
  display: flex;
  flex-direction: row;

  min-height: calc(100% - 40px);
  height: calc(100% - 40px);
}

/* - Navigation */
.gacha-home .home-navigation {
  width: 300px;
  height: 100%;
  overflow: auto;
  border-right: 2px solid #294767;
}

.gacha-home .home-navigation .filters-section {
  background: #0b1219;
  border-bottom: 1px solid #294767;
  cursor: pointer;
}

.gacha-home .home-navigation .filters-section h4 {
  padding-left: 10px;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
}

.gacha-home .home-navigation .filters-section .fa-bars {
  padding-right: 5px;
}

.gacha-home .home-navigation .filters-section .home-filter-view {
  border-top: 1px solid #CCC;
  font-size: 0.9em;
  padding: 10px 0 10px 5px;
}
.gacha-home .home-navigation .filters-section .home-filter-view .home-filter-group {
  padding-bottom: 5px;
}
.gacha-home .home-navigation .filters-section .home-filter-view .home-filter-group input[type="text"] {
  width: calc(100% - 15px);
}

.gacha-home .home-navigation ul,
.gacha-home-web-view ul.fusion-details {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.gacha-home .home-navigation ul li,
.gacha-home-web-view ul.fusion-details li {
  cursor: pointer;
  padding: 5px;
}
.gacha-home .home-navigation ul li:hover,
.gacha-home-web-view ul.fusion-details li:hover {
  background: #0c1219;
}
.gacha-home .home-navigation ul li .card-list-item,
.gacha-home-web-view ul.fusion-details li .card-list-item {
  display: flex;
  align-items: center;
}
.gacha-home .home-navigation ul li .card-list-item .fusion-icon,
.gacha-home-web-view ul.fusion-details li .card-list-item .fusion-icon {
  font-size: 0.75em;
  color: cyan;
  width: 15px;
}
.gacha-home .home-navigation ul li .card-list-item .level-icon,
.gacha-home-web-view ul.fusion-details li .card-list-item .level-icon {
  font-size: 0.5em;
  color: gold;
  width: 40px;
}
.gacha-home .home-navigation ul li .card-list-item .card-name,
.gacha-home-web-view ul.fusion-details li .card-list-item .card-name {
  font-size: 0.9em;
}
.gacha-home .home-navigation .fusion-dependencies,
.gacha-home-web-view .fusion-dependencies {
  padding-left: 15px;
}
.gacha-home .home-navigation .fusion-dependencies h5,
.gacha-home-web-view .fusion-dependencies h5 {
  margin-top: 1em;
  margin-bottom: 1em;
}

/* - Empty home (desktop only) */
.gacha-home .home-empty-view {
  width: calc(100% - 300px);
  font-size: 30px;
  color: #CCC;
}
.gacha-home .home-empty-view .filter-arrow {
  padding: 20px 0 0 70px;
  color: #3a5979;
}
.gacha-home .home-empty-view .filter-text {
  padding-left: 100px;
  padding-bottom: 50px;
}
.gacha-home .home-empty-view .pick-card-text {
  padding-left: 100px;
  padding-top: 50px;
}
.gacha-home .home-empty-view .pick-card-arrow {
  padding: 0 0 20px 70px;
  color: #3a5979;
}
.gacha-home .home-empty-view .details-text {
  text-align: center;
  padding-top: 40px;
}
.gacha-home .home-empty-view .details-text .fa-info-circle {
  padding-right: 10px;
}

/* - Common view */
.gacha-home .gacha-home-mobile-view .gacha-home-mobile-view-card-block,
.gacha-home .gacha-home-web-view .gacha-home-web-view-card-block {
  color: #333;
  margin-left: auto;
  margin-right: auto;
  width: 20vw;
  min-width: 200px;
}

/* - Web view */
.gacha-home .gacha-home-web-view {
  padding: 30px;
  width: calc(100% - 300px);
}

/* - Web view */
.gacha-home .gacha-home-mobile-view {
  padding: 10px;
  width: 100%
}

/* Mobile */
@media screen and (max-width: 600px) {
  .gacha-home .home-navigation {
    width: 100%;
  }
}
