.card-container {
  font-family: "Agency FB";
  background: #dadada;
  border-radius: 5%;
  display: inline-block;
  border: 1px solid #666;
  max-width: 470px;
  width: 100%;
  padding-top: 163%;
  position: relative;
  max-height: 100%;
}

.card-container.scale-horizontally.big {
  width: 100vw;
}
.card-container.scale-horizontally.half {
  width: 50vw;
}
.card-container.scale-horizontally.quarter {
  width: 20vw;
  min-width: 200px;
}

.card-container.scale-vertically.quarter {
  height: 100%;
  width: calc(20vh * 0.61);
  padding-top: 0;
}

.card-container.scale-vertically.half {
  height: 100%;
  width: calc(50vh * 0.61);
  padding-top: 0;
}
.card-container.scale-vertically.big {
  height: 100%;
  width: calc(80vh * 0.61);
  padding-top: 0;
}
.card-container.gold {
  background: #f1b500;
}

.card {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.card-header {
  display: flex;
  font-size: 0.75em;
  font-weight: bold;
}

.card-fusion {
  padding: 2% 4%;
}

.card-id {
  padding: 2% 4%;
  margin-right: 0;
  margin-left: auto;
}

.card-img {
  margin-left: 4%;
  margin-right: 4%;
  height: 42%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.card-title {
  font-size: 1em;
  font-weight: bold;
  padding: 0 4%;
  height: 10%;
  line-height: 200%;
}

.card-title svg {
  width: 100%;
  height: 100%;
}

.card-separator {
  padding-left: 30%;
  padding-right: 30%;
  height: 6%;
  padding-top: 6%;
  box-sizing: border-box;
}

.card-line {
  width: 100%;
  height: 1px;
  background: #e01c3c;
  -webkit-box-shadow: 0px 0px 15px 3px #e01c3c;
  -moz-box-shadow: 0px 0px 15px 3px #e01c3c;
  box-shadow: 0px 0px 15px 3px #e01c3c;
}

.card-description {
  width: 100%;
  box-sizing: border-box;
  padding: 2.5% 4%;
  height: 23%;
}

.card-stars {
  text-align: center;
  height: 10%;
}

.card-star {
  background: url("../assets/star.png");
  display: inline-block;
  height: 100%;
  width: 15%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.card.gold .card-star {
  background: url("../assets/star-gold.png");
}
