nav {
  height: 40px;
  padding: 5px;
  box-sizing: border-box;
  margin: 0;
  width: 100%;
  background: #111;
  color: #DDD;
  border-bottom: 1px solid #294767;
}

nav ul {
  padding: 0 20px;
  margin: 0;
  list-style-type: none;
  display: flex;
  height: 100%;
  align-items: center;
}

nav ul li {
  padding: 0 10px;
}

nav ul li a {
  text-decoration: none;
  color: inherit;
}

nav ul li a:hover {
  text-decoration: underline;
}
